import React from 'react'
import { useHistory } from 'react-router-dom'
import GeneralButton from '../components/generalbutton'
import Messager from '../components/messager'
import Title from '../components/title'

function DashboardContainer ({ children }) {
  const history = useHistory()
  const onNextFunciton = () => {
    localStorage.removeItem('access_token')
    history.push('/')
  }
  return (
    <div>
      <div className='bg-blue-100 min-h-screen w-full'>
        <nav className='w-full flex px-2 py-2 justify-between items-center content-center bg-white shadow-md'>
          <div className=''>
            <Title text='dashboard' />
          </div>
          <div className='w-full justify-end items-center content-center flex'>
            <GeneralButton
              onNextFunciton={() => {
                onNextFunciton()
              }}
              label='Logout'
            />
          </div>
        </nav>
        <div>{children}</div>
      </div>
    </div>
  )
}

export default DashboardContainer
