import React from 'react'

function TextInput ({ label, value, setValue, placeholder, name, type }) {
  return (
    <div className='py-2 px-2'>
      <label
        className='text-blue-500 text-sm mb-1 px-1'
        htmlFor={name ? name : 'textinput'}
      >
        {label}
      </label>
      <input
        className='w-full flex px-2 focus:outline-none border border-blue-500 rounded-md py-2 bg-blue-50 focus:bg-white focus:shadow-sm'
        placeholder={placeholder}
        value={value}
        type={type}
        onChange={e => {
          setValue(e.target.value)
        }}
      />
    </div>
  )
}

export default TextInput
