import React from 'react'
import TextInput from './textinput'

function AddForm ({
  keyword,
  setkeyword,
  fullword,
  setfullword,
  rate,
  setrate
}) {
  return (
    <div className='flex flex-col w-full'>
      <TextInput
        value={keyword}
        setValue={setkeyword}
        label='Enter Keyword'
        placeholder='keyword'
        type='text'
      />
      <TextInput
        type='text'
        label='Enter full word'
        placeholder='fullword'
        value={fullword}
        setValue={setfullword}
      />
      <TextInput
        type='text'
        label='Enter Rate'
        placeholder='rate'
        value={rate}
        setValue={setrate}
      />
    </div>
  )
}

export default AddForm
