import React, { useEffect, useState } from 'react'
import { API } from '../../api'
import AddForm from '../../components/addform'
import DeleteForm from '../../components/deleteform'
import EditForm from '../../components/editform'
import LoadingScreen from '../../components/loading'
import Modal from '../../components/modal'

import DashboardContainer from '../../templates/dashboardcontainer'

function Exchange () {
  const [addForm, setAddForm] = useState(false)
  const [editForm, setEditForm] = useState(false)
  const [deleteForm, setDeleteForm] = useState(false)
  const [id, setId] = useState('')
  const [keyword, setKeyword] = useState('')
  const [full_word, setFull_word] = useState('')
  const [rate, setRate] = useState('')
  const [loading, setLoading] = useState(false)
  const [exchanges_rates, setExchanges_rates] = useState([])
  const [refresh, setRefresh] = useState(false)

  useEffect(() => {
    getRates()
  }, [refresh])

  const getRates = () => {
    setLoading(true)
    API.get('/exchange')
      .then(res => {
        setExchanges_rates(res.data.data)
        setLoading(false)
      })
      .catch(err => {
        console.log(err)
        setLoading(false)
      })
  }

  const onShowAdd = () => {
    setAddForm(true)
    setId('')
    setKeyword('')
    setFull_word('')
    setRate('')
  }
  const onNextAdd = () => {
    setLoading(true)
    setAddForm(false)
    API('/exchange', {
      method: 'POST',
      data: {
        keyword: keyword,
        full_word: full_word,
        rate: rate
      }
    })
      .then(res => {
        console.log(res.data)
        if (res.data.status === 1) {
          alert('success')
        }
      })
      .catch(err => {
        console.log(err)
        alert('error')
      })
      .finally(() => {
        setLoading(false)
        setRefresh(!refresh)
      })
  }
  const onNextEdit = () => {
    setLoading(true)
    setEditForm(false)
    API('/exchange', {
      method: 'PUT',
      data: {
        id: id,
        keyword: keyword,
        full_word: full_word,
        rate: rate
      }
    })
      .then(res => {
        console.log(res.data)
        if (res.data.status === 1) {
          alert('success')
        }
      })
      .catch(err => {
        console.log(err)
        alert('error')
      })
      .finally(() => {
        setLoading(false)
        setRefresh(!refresh)
      })
  }

  const onNextDelete = () => {
    setLoading(true)
    setDeleteForm(false)

    API('/exchange', {
      method: 'DELETE',
      data: {
        id: id
      }
    })
      .then(res => {
        console.log(res.data)
        if (res.data.status === 1) {
          alert('success')
        }
      })
      .catch(err => {
        console.log(err)
        alert('error')
      })
      .finally(() => {
        setLoading(false)
        setRefresh(!refresh)
      })
  }
  const onShowEdit = data => {
    setEditForm(true)
    setId(data._id)
    setKeyword(data.keyword)
    setFull_word(data.full_word)
    setRate(data.rate)
  }
  const onShowDelete = id => {
    setDeleteForm(true)
    setId(id)
  }

  return (
    <DashboardContainer>
      <div>
        {loading ? <LoadingScreen /> : null}
        <h5 className='text-blue-500 py-2 px-2 text-center'> Exchange Rates</h5>
        <div className='w-full'>
          <div class='container w-full md:w-4/5 xl:w-3/5  mx-auto px-2'>
            <div className='w-full justify-end flex'>
              <button
                onClick={() => {
                  onShowAdd()
                }}
                className='bg-blue-500  rounded-md py-2 px-2 mb-2 text-white hover:bg-blue-600 '
              >
                <svg
                  xmlns='http://www.w3.org/2000/svg'
                  width='16'
                  height='16'
                  fill='currentColor'
                  className='bi bi-plus-square'
                  viewBox='0 0 16 16'
                >
                  <path d='M14 1a1 1 0 0 1 1 1v12a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1h12zM2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H2z' />
                  <path d='M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4z' />
                </svg>
              </button>
            </div>
            <div class='p-8 mt-6 lg:mt-0 rounded shadow bg-white'>
              <table class='stripe hover' className='w-full pt-1 pb-1'>
                <thead>
                  <tr className='text-left py-2'>
                    <th>#</th>
                    <th>keyword</th>
                    <th>full word</th>
                    <th>rate</th>
                    <th>updated at</th>
                    <th>created by</th>
                    <th className='text-center'>action</th>
                  </tr>
                </thead>
                <tbody>
                  {exchanges_rates.map((r, i) => (
                    <tr className='border-b py-2' key={i}>
                      <td>{i + 1}</td>
                      <td>{r.keyword}</td>
                      <td>{r.full_word}</td>
                      <td>{r.rate}</td>
                      <td>{new Date(r.updatedAt).toTimeString()}</td>
                      <td>{r.user.username}</td>
                      <td className='w-full flex justify-evenly items-center content-center px-2 py-2'>
                        <button
                          onClick={() => {
                            onShowEdit(r)
                          }}
                          className='bg-yellow-400 text-white rounded-md px-2 py-2 focus:outline-none hover:bg-yellow-600'
                        >
                          <svg
                            xmlns='http://www.w3.org/2000/svg'
                            width='16'
                            height='16'
                            fill='currentColor'
                            className='bi bi-pencil-square'
                            viewBox='0 0 16 16'
                          >
                            <path d='M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z' />
                            <path
                              fill-rule='evenodd'
                              d='M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z'
                            />
                          </svg>
                        </button>
                        <button
                          onClick={() => {
                            onShowDelete(r._id)
                          }}
                          className='bg-red-400 text-white rounded-md px-2 py-2 focus:outline-none hover:bg-red-600'
                        >
                          <svg
                            xmlns='http://www.w3.org/2000/svg'
                            width='16'
                            height='16'
                            fill='currentColor'
                            className='bi bi-trash'
                            viewBox='0 0 16 16'
                          >
                            <path d='M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z' />
                            <path
                              fill-rule='evenodd'
                              d='M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z'
                            />
                          </svg>
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
      <Modal
        show={addForm}
        setShow={setAddForm}
        onNextFunction={() => {
          onNextAdd()
        }}
        title='Add Rate'
      >
        <AddForm
          fullword={full_word}
          setfullword={setFull_word}
          keyword={keyword}
          setkeyword={setKeyword}
          rate={rate}
          setrate={setRate}
        />
      </Modal>
      <Modal
        show={editForm}
        setShow={setEditForm}
        onNextFunction={() => {
          onNextEdit()
        }}
        title='Edit Rate'
      >
        <EditForm
          fullword={full_word}
          setfullword={setFull_word}
          keyword={keyword}
          setkeyword={setKeyword}
          rate={rate}
          setrate={setRate}
          id={id}
          setId={setId}
        />
      </Modal>
      <Modal
        show={deleteForm}
        setShow={setDeleteForm}
        onNextFunction={() => {
          onNextDelete()
        }}
        title='Delete Rate'
      >
        <DeleteForm />
      </Modal>
    </DashboardContainer>
  )
}

export default Exchange
