import React from 'react'

function GeneralButton ({ onNextFunciton, label, disabled }) {
  return (
    <div className='px-2 py-2'>
      <button
        disabled={disabled}
        onClick={() => {
          onNextFunciton()
        }}
        className='bg-blue-500 rounded-md focus:outline-none hover:bg-blue-700 text-white round-md px-2 py-2 flex w-full justify-center items-center content-center'
      >
        {label}
      </button>
    </div>
  )
}

export default GeneralButton
